.nav {
  position: sticky;
  top: 0;
  left: 0;
  display: flex;
  justify-content: space-between;
  background-color: rgb(255, 255, 255, 0.95);
  width: 100%;
  max-width: 1400px;
  margin: 0 auto;
  height: 100px;
  z-index: 10;
}

.nav ul {
  display: flex;
  width: 20em;
  justify-content: center;
  align-items: center;
  padding: 0;
}

.nav li {
  list-style: none;
  margin: 0 1em;
}

.link {
  margin: 0 1em;
  color: black;
  text-decoration: none;
}

.link--nav {
  font-size: 22px;
  font-weight: bold;
}

.link--nav:hover {
  color: grey;
}

.logo-div {
  display: flex;
  justify-content: center;
  align-items: center;
}

.logo {
  font-size: 34px;
  font-weight: bold;
}

.page-title {
  display: flex;
  margin: 0 1em;
  align-items: center;
}

.active {
  color: grey;
}

.menu-icon {
  width: 40px;
}

@media (max-width: 800px) {
  .nav {
    width: 100%;
  }

  .menu-icon-div {
    display: flex;
    align-items: center;
    cursor: pointer;
  }

  .menu-icon {
    margin: 0 50px;
  }

  .title-div {
    justify-content: start;
    align-items: center;
  }

  .title {
    font-size: 30px;
  }
}
