:root {
  --dropdown-length: 1.5s;
}

.container{
  display: flex;
  justify-content: center;
  align-items: flex-end;
  min-height: 250px;
  max-height: 400px;
  height: 40vh;
}

.welcome-message {
  text-align: center;
  animation: dropdown var(--dropdown-length) cubic-bezier(0,0,0.35,2);
}

#top-line{
  font-size: 34px;
  font-weight: bold;
}

#second-line {
  font-size: 22px;
  font-weight: bold;
}

.stacks {
  display: flex;
  justify-content: space-evenly;
  margin: 0 auto;
  margin-top: 50px;
  padding: 0;
  width: 90%;
}

.stack {
  font-size: 18.72px;
  font-weight: bold;
  animation: stack-fly-in 0.5s ease-out;
  animation-fill-mode: backwards;
  animation-delay: var(--dropdown-length);
  list-style: none;
}

.stack:nth-child(2) {
  animation-delay: calc(var(--dropdown-length) + 0.2s);
}
.stack:nth-child(3) {
  animation-delay: calc(var(--dropdown-length) + 0.4s);
}

.stack:nth-child(4) {
  animation-delay: calc(var(--dropdown-length) + 0.6s);
}

.stack:nth-child(5) {
  animation-delay: calc(var(--dropdown-length) + 0.8s);
}

@media (max-width: 800px) {
  .welcome-message {
    width: 100vw;
    margin: 0 auto;
    /* font-size: 0.8rem; */
  }

  #top-line {
    font-size: 27.2px;
  }

  #second-line {
    font-size: 17.6px;
  }

  .stacks {
    width: 280px;
  }

  .stack {
    font-size: 14.98px;
  }
}

/*KEYFRAMES*/
@keyframes dropdown {
  0%{
    transform: translateY(-400px);
  }

  80% {
    transform: translateY(0);
  }

  90% {
    transform: translateY(-15px);
  }

  100%{
    transform: translateY(0);
  }
}

@keyframes stack-fly-in {
  0%{
    transform: translateX(100%);
    opacity: 0;
  }

  100%{
    transform: translateX(0);
  }
}
